export default {
	data() {
		return {
			data:[]
		};
	},
	mounted() {
		this.init()
	},
	methods:{
		//
		init:function(){
			uni.request({
			        url: this.$store.state.url + 'CRM/GetCusLinkManMini',
			        method: 'GET',
			        data: {
			            cusId : this.$store.state.Customercode
			        },
			        header: {
			            'Token': this.$store.state.token
			        },
			        success: (res) => {
			            var asd = JSON.parse(res.data).Code 
			            if (asd == 0) {
							console.log(JSON.parse(res.data).Data.Dtos)
							this.data = JSON.parse(res.data).Data.Dtos
			            } else {
			                uni.showToast({
			                    title: JSON.parse(res.data).Msg || '错误',
			                    icon: "none"
			                })
			            }
			        }
			    });

		},
		//切换联系人
		qie:function(item){
			this.$store.state.lianxir = item
			uni.navigateTo({url:"./Buy"})
		}
		
		
	}
}
